document.addEventListener('DOMContentLoaded', function() {
    
    iniciarApp();
});

function iniciarApp() {
    crear();
    let calcular = document.querySelector('.boton--calcular');
    calcular.addEventListener('click', calculate);
    let limpiar = document.querySelector('.boton--limpiar');
    limpiar.addEventListener('click', clear);
}

let juegos; 
let i;
let valorApuesta = 1;




function crear() {
    juegos = parseInt(prompt('Ingrese la Cantidad de Juegos a Apostar, hasta un Máximo de 20 juegos. Ejemplo: 5  para 5 logros.'));
    if(juegos >= 1 && juegos <= 20) {
        for( i = 1; i <= juegos; i++) {
            let logro = document.createElement('DIV');
            logro.classList.add('formulario__campo', 'clear-campos');
            logro.innerHTML = `
            <label class="formulario__label"  for="${i}">${i}</label>
            <input class="formulario__imput juegos" type="number" name="${i}" id="${i}">
            `
            let formularioLogros = document.querySelector('.formulario__logros');
                formularioLogros.appendChild(logro);
        }
    }  else {
       alert('Nuestra Calculadora, realiza un máximo de 20 Apuestas. Por Favor ingrese un valor menor o igual a 20. Ejemplo: 3 para 3 logros.')
        crear();
    }
        
}

function calculate() {
    const apuesta = document.querySelector('.formulario__imput--apuesta');
    valorApuesta = apuesta.value;
    let resultadoArray = [];
    let total = document.querySelector('.formulario__imput--total');
    let mensaje = document.querySelector('.mensaje__parrafo');
    let veces;
    let alertaLogro = document.querySelector('.mensaje__alerta--logro');
    let alertaApuesta = document.querySelector('.mensaje__alerta--apuesta');


    if (!valorApuesta){
        alertaApuesta.classList.remove('d-none');
        alertaApuesta.classList.add('bg-alerta');
    } else {
        alertaApuesta.classList.remove('bg-alerta');
        alertaApuesta.classList.add('d-none');
    }
    
    function repeat(resultado, valorApuesta) {
        resultado = valorApuesta * resultado;
        total.value = resultado.toFixed(2);
        veces = (resultado / valorApuesta).toFixed(2);
        if(veces > 0)  {
            mensaje.textContent = `Su apuesta paga ${veces} veces el valor del Monto Apostado`;
        }
        return;
    }

    
    if(juegos) {
        let valor = document.querySelectorAll('.juegos');    
        valor.forEach(element => {
            if(element.value) {
                alertaLogro.classList.remove('bg-alerta');
                alertaLogro.classList.add('d-none');
                if (element.value > 0) {
                    element.value = element.value  / 100 + 1 ;
                } else {
                    element.value = Math.abs (100 / element.value) + 1 ;
                }   
            }  else {
                alertaLogro.classList.remove('d-none');
                alertaLogro.classList.add('bg-alerta');
            }
            resultadoArray.push(element.value);
        });

        let multiplicacion = 1;

        for (let j = 0; j < resultadoArray.length; j++ ) {
            const elemento = resultadoArray[j];
            multiplicacion *= elemento;
        } 
        if(valorApuesta) {
           
            repeat(multiplicacion, valorApuesta);
        }     
       
    }
           
}

function clear () {
    const campos = document.querySelectorAll('.formulario__imput');
    campos.forEach(campo => {
        campo.value = '';
    });
    mensaje = document.querySelector('.mensaje__parrafo');
    mensaje.textContent = '';
    const clearJuegos = document.querySelectorAll('.juegos');
    clearJuegos.forEach(element => {
        element.classList.remove('juegos');
    });
    const clearCampo = document.querySelectorAll('.clear-campos');
    clearCampo.forEach(element => {
        element.classList.add('d-none');
    });
    crear();
}





